<template>
  <div>
    <my-toolbar v-on:to-admin="onToAdmin"/>
    <home-par/>
    <home-sm-menu v-if="$vuetify.breakpoint.smAndDown"/>
    <v-container fluid v-bind:class="activeColor" class="darken-2 py-4">
      <v-layout row>
        <v-flex>
          <div class="title text-md-center my-0 font-weight-light white--text">Made by</div>
          <div class="display-3 text-md-center mb-4 font-weight-bold white--text">Yuni Rahmawati</div>
        </v-flex>
      </v-layout>
      <v-layout row class="px-2">
          <v-flex
            md4
            align-center
            justify-center
            layout
            text-xs-center
            v-for="(avatar, i) in images"
            :key="i"
          >
            <v-scale-transition>
              <v-avatar
                v-if="activeImage == avatar"
                size="200"
                v-bind:class="{ 'elevation-24 size-increase': isMouseOver }"
                class="main-avatar mb-4 mt-2" @click="onClickPerson"
                @mouseover="isMouseOver = true"
                @mouseout="isMouseOver = false">
                <img :src="'/static/' + avatar" alt="avatar">
              </v-avatar>
          </v-scale-transition>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import MyToolbar from './MyToolbar'
import HomePar from './HomePar'
import HomeSmMenu from './HomeSmMenu'

export default {
  name: 'Home',
  components: {
    HomePar,
    HomeSmMenu,
    MyToolbar
  },
  data () {
    return {
      colors: ['yellow', 'teal', 'brown', 'red', 'pink', 'cyan', 'indigo', 'purple'],
      images: [
        'avatar-1.jpg',
        'avatar-2.jpg',
        'avatar-3.jpg',
        'avatar-4.jpg'
      ],
      activeCI: 0,
      activeI: 0,
      iId: null,
      isMouseOver: true
    }
  },
  computed: {
    activeColor () {
      return this.colors[this.activeCI]
    },
    activeImage () {
      return this.images[this.activeI]
    }
  },
  methods: {
    onToAdmin () {
      window.location = '/admin/index.html'
    },
    onClickPerson () {
      window.open('https://web.facebook.com/yuni.rahmawati.71653', '_black')
    }
  },
  mounted () {
    setInterval(() => {
      let nextCi = (this.activeCI + 1) % this.colors.length
      this.activeCI = nextCi

      let nextImage = (this.activeI + 1) % this.images.length
      this.activeI = nextImage
    }, 2000)
  },
  beforeDestroy () {
    if (this.iId !== null) {
      clearInterval(this.iId)
    }
  }
}
</script>

<style>
.main-avatar {
  transition: all .3s;
}
.main-avatar.size-increase {
  transform: scale(1.1);
  cursor: pointer;
}
.v-parallax__content {
  padding: 0;
  background-color: rgba(0,0,0, .3);
}
</style>



