<template>
  <div class="white">
    <side-nav/>

    <v-container fluid grid-list-md class="pa-0 ma-0">
      <v-layout row>
        <v-flex md3>
          <v-container fluid>
            <v-text-field
              append-icon="mic"
              class="my-0"
              flat
              label="Search"
              prepend-inner-icon="search"
              v-model="keyword"
            ></v-text-field>
            <v-list 
              dense
              two-line >
              <v-subheader>
                List Lahan
              </v-subheader>

              <v-list-tile
                avatar
                v-for="(item, index) in filteredItems"
                :key="index"
                @click="changeActiveLahan(item._id)"
                v-bind:light="index == activeLahanIndex"
                v-bind:class="{ 'blue': index == activeLahanIndex }"
              >
                <v-list-tile-avatar>
                  <img :src="item.avatar">
                </v-list-tile-avatar>

                <v-list-tile-content>
                  <v-list-tile-title v-html="item.lokasi"></v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-list>
          </v-container>
        </v-flex>
        <v-flex md9>
          <v-toolbar id="tab-toolbar" dark flat color="pink">
            <v-toolbar-title>
              <div v-if="activeLahanIndex != null" class="title">{{ activeLahan.lokasi }}</div>
              <div v-else>Silahkan Pilih lokasi</div>
            </v-toolbar-title>
          </v-toolbar>
          <v-tabs
            dark
            color="pink lighten-1"
            grow
            dense
          >
            <!-- <div 
              style="flex-grow: 1; line-height: normal; display: flex; align-items: center; padding-left: 12px;"
            >
              <div v-if="activeLahanIndex != null" class="title">{{ activeLahan.lokasi }}</div>
              <div v-else>Silahkan Pilih lokasi</div>
            </div> -->
            <v-tabs-slider></v-tabs-slider>

            <v-tab href="#mymap">
              Map
            </v-tab>

            <v-tab href="#tab-2">
              Info
            </v-tab>

            <v-tab href="#tab-3">
              Galeri
            </v-tab>

            <v-tab-item id="mymap">

            </v-tab-item>

            <v-tab-item id="tab-2">
              <v-container>
                <v-text-field 
                  label="Lokasi"
                  :value="activeLahan.lokasi"
                  readonly></v-text-field>

                <v-text-field 
                  label="Curah Hujan"
                  :value="activeLahan.curahHujan"
                  readonly></v-text-field>

                <v-text-field 
                  label="Sistem Pengairan"
                  :value="activeLahan.sistemPengairan"
                  readonly></v-text-field>

                <v-text-field 
                  label="Sumber Air"
                  :value="activeLahan.sumberAir"
                  readonly></v-text-field>

                <v-text-field 
                  label="Kemiringan Lahan"
                  :value="activeLahan.kemiringanLahan"
                  readonly></v-text-field>

                <v-text-field 
                  label="Tekstur Tanah"
                  :value="activeLahan.teksturTanah"
                  readonly></v-text-field>

                <v-text-field 
                  label="C-Organik"
                  :value="activeLahan.corganik"
                  readonly></v-text-field>

                <v-text-field 
                  label="Kedalaman Tanah"
                  :value="activeLahan.kedalamanTanah"
                  readonly></v-text-field>
                  
              </v-container>
            </v-tab-item>

            <v-tab-item id="tab-3">
              <galeri-view :medias="medias" />
            </v-tab-item>
          </v-tabs>
        </v-flex>
      </v-layout>
      
    </v-container>
  </div>
</template>

<script>
import Vue from 'vue'
import MyToolbar from './MyToolbar'
import GaleriView from './GaleriView'
import SideNav from './SideNav'
import { getData, getMedias, getAvatar } from '../services/lahan'

const defLahan = {
  lokasi: 'oesapa',
  avatar: 'https://cdn.vuetifyjs.com/images/lists/1.jpg',
  lat: -10.179173,
  long: 123.591715,
  curahHujan: 448,
  sistemPengairan: 'Irigasi Permukaan',
  sumberAir: 'Banyak',
  kemiringanLahan: 'Datar',
  teksturTanah: 'Halus',
  corganik: 'Tersedia',
  bahayaBencana: 'Ringan',
  kedalamanTanah: 48
}

export default {
  components: {
    MyToolbar,
    GaleriView,
    SideNav
  },
  data () {
    return {
      items: [],
      sidebarWidth: 400,
      activeLahanIndex: null,
      keyword: '',
      medias: [],
      map: null,
      mapInfo: null
    }
  },
  computed: {
    activeLahan () {
      if (this.activeLahanIndex == null) {
        return defLahan
      } else {
        return this.items[this.activeLahanIndex]
      }
    },
    filteredItems () {
      let upperIndex = this.keyword.toUpperCase()
      return this.items.filter(it => {
        let loc = it.lokasi
        return loc.toUpperCase().indexOf(upperIndex) !== -1
      })
    }
  },

  methods: {
    sayHi () {
      console.log('Hi')
    },
    getAllData () {
      return getData().then(items => {
        this.items = items
        this.items.forEach((v, i) => {
          getAvatar(v._id).then(data => {
            this.items[i].avatar = data.url
          })
          .catch(err => {
            console.log(err)
          })
        })
        return this.items
      })
    },
    createMap (data) {
      let defLoc = [-10.102195, 123.878633]
      var vueThis = this
      if (this.activeLahanIndex != null) {
        // Put location here. activeLahan is defined.
        defLoc[0] = this.activeLahan.long
        defLoc[1] = this.activeLahan.lat
      }
      // eslint-disable-next-line
      this.map = L.map('mymap').setView(defLoc, 12)
      // eslint-disable-next-line
      L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}', {
        attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
        maxZoom: 18,
        id: 'mapbox.streets',
        accessToken: 'pk.eyJ1Ijoiam9tZXRhIiwiYSI6ImNqbTB4eWtqNzB5dzAzcHJ2NGVoaW5wcXAifQ.yXaPLSgJXcn0shC2nYDU0g'
      }).addTo(this.map)

      let gFeatures = data.map((d, index) => {
        return {
          type: 'Feature',
          properties: {
            index: index,
            id: d._id,
            name: d.lokasi,
            amenity: 'Baseball Stadium',
            lokasi: d.lokasi,
            kedalamanTanah: d.kedalamanTanah,
            curahHujan: d.curahHujan,
            sumberAir: d.sumberAir,
            sistemPengairan: d.sistemPengairan,
            kemiringanLahan: d.kemiringanLahan,
            teksturTanah: d.teksturTanah,
            corganik: d.corganik,
            bahayaBencana: d.bahayaBencana,
            popupContent: 'Lokasi: ' + d.lokasi,
            urutan: d.urutan
          },
          geometry: {
            type: 'Point',
            coordinates: [d.long, d.lat]
          }
        }
      })
      // eslint-disable-next-line
      let geojsonMarkerOptions = {
        radius: 8,
        fillColor: '#ff7800',
        color: '#000',
        weight: 1,
        opacity: 1,
        fillOpacity: 0.8
      }

      // eslint-disable-next-line
      var info = L.control({position: 'topright'})
      info.onAdd = function (map) {
        // eslint-disable-next-line
        this._div = L.DomUtil.create('div', 'info-map')
        this.update()
        return this._div
      }
      info.update = function (props) {
        let template = '<h5>Arahkan mouse pada lahan!</h5>'
        // eslint-disable-next-line
        if (props) {
          template = `
            <div>
              Lokasi: ${props.lokasi}
            </div>
            <div>
              Curah Hujan: ${props.curahHujan}
            </div>
            <div>
              Kedalaman Tanah: ${props.kedalamanTanah}
            </div>
            <div>
              Sumber Air: ${props.sumberAir}
            </div>
            <div>
              Sistem Pengairan: ${props.sistemPengairan}
            </div>
            <div>
              Kemiringan Lahan: ${props.kemiringanLahan}
            </div>
            <div>
              Tekstur Tanah: ${props.teksturTanah}
            </div>
            <div>
              Bahaya Bencana: ${props.bahayaBencana}
            </div>
          `
        }
        this._div.innerHTML = '<h4>Data Lahan</h4>' + template
      }
      info.addTo(this.map)
      vueThis.mapInfo = info

      function onMouseClick (e) {
        var layer = e.target
        info.update(layer.feature.properties)
        vueThis.changeActiveLahan(layer.feature.properties.id)
      }

      function onEachFeature (feature, layer) {
        layer.on({
          click: onMouseClick
        })
      }
      // eslint-disable-next-line
      let geojsonLayer = L.geoJSON(gFeatures, {
        pointToLayer: function (feature, latlang) {
          let ur = '-'
          // eslint-disable-next-line
          if (feature.properties.urutan != undefined && feature.properties.urutan != null) {
            ur = feature.properties.urutan
          }
          // eslint-disable-next-line
          return new L.Marker(latlang, {
            // eslint-disable-next-line
            icon: L.divIcon({
              className: 'my-marker-icon',
              html: `<div class="circle-it">${ur}</div>`
            })
          })
        },
        onEachFeature: onEachFeature
      }).addTo(this.map)
    },
    updateMap () {
      if (this.activeLahanIndex === null) {
        return
      }
      this.map.panTo([this.activeLahan.lat, this.activeLahan.long], { animation: true, duration: 2.0 })
      this.map.setZoom(14)
    },
    findLocByLatLang (latLang) {
      var selIndex = null
      for (let i = 0; i < this.items.length; i++) {
        let it = this.items[i]
        if (it.lat === latLang.lat && it.long === latLang.long) {
          selIndex = i
        }
      }
      if (selIndex == null) {
        return
      }
      this.activeLahanIndex = selIndex
    },
    changeActiveLahan (id) {
      let items = this.items
      let index = null
      for (let i = 0; i < items.length; i++) {
        if (items[i]._id === id) {
          index = i
          break
        }
      }
      if (index === null) {
        return
      }
      this.activeLahanIndex = index
      Vue.nextTick(() => {
        this.updateMap()
        this.loadMedias(id)
        this.mapInfo.update(this.activeLahan)
      })
    },
    loadMedias (id) {
      if (id === null | id === undefined) {
        return Promise.resolve([])
      }
      return getMedias(id)
        .then(items => {
          this.medias = items
        })
        .catch(err => {
          console.log(err)
          return []
        })
    }
  },

  mounted () {
    // eslint-disable-next-line
    this.getAllData()
      .then(data => this.createMap(data))
      .catch(err => {
        console.log(err)
      })
  }
}
</script>

<style>
/* .v-list__tile.v-list__tile--avatar.theme--light {
  border-bottom: 1px solid #DDD;
} */
#mymap {
  height: 500px;
}

/* Imitate button look, and make it match the toolbar height */
#add-area {
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

#tab-toolbar .v-toolbar__content {
  justify-content: center;
}
div.info-map {
  padding: 6px 8px;
  font: 14px/16px Arial, Helvetica, sans-serif;
  background: white;
  background: rgba(255,255,255,0.8);
  box-shadow: 0 0 15px rgba(0,0,0,0.2);
  border-radius: 5px;
}
div.info-map h4 {
  margin: 0 0 5px;
  color: #777;
}
div.info-map > div {
  margin-top: 8px;
  margin-bottom: 8px;
  font-size: 16px;
}

.circle-it {
  height: 20px;
  width: 20px;
  border-radius: 10px;
  background-color: purple;
  color: white;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>



